import React, { useState, useMemo } from 'react'
import uuid from 'uuid/dist/v4'
import { useWeb3React } from '@web3-react/core'
import { Tabs, Table, Divider, Modal, Input, message } from 'antd'
import { HecoContract, OecContract, HecoProvider, OecProvider, erc20abi, hecoAddr, oecAddr } from '../../../public/Web3'
import { signCall } from '../../utils/ethRequest'
import { toBN, injected, BN_to_str } from '../../utils/utils'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { TabPane } = Tabs;



const AmountModal = ({
  visible,
  type,
  item,
  provider,
  handleOk,
  handleCancel
}) => {
  const [amount, setAmount] = useState()
  const [balance, setBalance] = useState(null)
  const [error, setError] = useState(false)

  useMemo(() => {
    if (!item || !type) return;
    const { token, liquidity_token, token_decimals } = item;
    const ethereum = window.ethereum
    const cubeContract = type === 'save' ? new provider.eth.Contract(erc20abi, token) : new provider.eth.Contract(erc20abi, liquidity_token);
    cubeContract.methods.balanceOf(ethereum.selectedAddress).call().then(result => {
      const balance = BN_to_str(result, token_decimals)
      setBalance(balance)
    })

  }, [item, type])

  const handleChange = (e) => {
    setError(false)
    const { value } = e.target
    if (Number(value) > balance) return setError(true)
    setAmount(Number(value))
  }
  
  const onOk = () => {
    if (!amount) return handleCancel()
    handleOk(amount)
  }

  return (
    <Modal
      visible={visible}
      onOk={onOk}
      onCancel={handleCancel}
      getContainer={false}
      closable={false}
    >
      <div className={styles.amount}>
        <Input style={{ width: '80%'}} onChange={handleChange} />
        <div>剩余：{balance}</div>
        {
          error ? <div className={styles.errorTip}>输入数量不能大于剩余数量</div> : null
        }
      </div>
      
    </Modal>
  )
}

const Gunpool = (props) => {
  const { activate, active, account, library } = useWeb3React()

  const [visible, setVisible] = useState(false)
  const [item, setItem] = useState(null)
  const [type, setType] = useState('')
  const [ctrlFlag, setCtrlFlag] = useState(true)
  const [tabKey, setTabKey] = useState('HECO');
  const [tableData, setTableData] = useState([])

  const {myContract, myProvider, router_addr} = useMemo(() => {
    let contract;
    let provider;
    let addr;
    switch (tabKey) {
      case 'HECO':
        contract = HecoContract
        provider = HecoProvider
        addr = hecoAddr
        break;
      case 'OEC':
        contract = OecContract
        provider = OecProvider
        addr = oecAddr
        break;
      default:
        contract = HecoContract
        provider = HecoProvider
        addr = hecoAddr
        break;
    }
    return { myContract: contract, myProvider: provider, router_addr: addr }
  }, [tabKey])

  useMemo(() => {

    if (!ctrlFlag) return
    myContract.methods.getPoolInfos().call().then((result) => {
   
      const temp = result.map(item => {
        const { token,equity, symbol,liquidity_decimals, apy, token_decimals, liquidity_token  } = item;
        return {
          token,
          equity,
          symbol,
          liquidity_decimals,
          apy,
          token_decimals,
          liquidity_token
        }
      })
   
      setTableData(temp)
      setCtrlFlag(false)
    })
  }, [tabKey, ctrlFlag])

  const columns = [{
    title: 'token',
    dataIndex: 'symbol',
    width: '30%'
  }, {
    title: 'TVL',
    dataIndex: 'equity',
    width: '30%',
    render: (num, record) => BN_to_str(num, record.token_decimals)
  }, {
    title: 'APY',
    dataIndex: 'apy',
    width: '30%',
    render: text => `${(text / 100).toFixed(2)}%`
  }, {
    title: '操作',
    dataIndex: 'operation',
    width: '10%',
    render: (_, record, index) => {
      return (
        <div className={styles.group}>
          <div onClick={() => {handleCtrl(record, 'save')}}>存</div>
          <Divider type="vertical" />
          <div onClick={() => {handleCtrl(record, 'take')}}>取</div>
        </div>
      )
    }
  }]

  const handleCtrl = async (item, type) => {
    if (!active) {
      await activate(injected, walletError => {
        if (walletError.message.includes('user_canceled')) {
          return alert('You canceled the operation, please refresh and try to reauthorize.')
        }
        alert(`Failed to connect: ${walletError.message}`)
      })
    }
    setItem(item)
    setType(type)
    setVisible(true)
  }

  const handleOk = async (money) => {
    const { token, token_decimals, liquidity_token } = item
    const ethereum = window.ethereum
    const saveCntr = new myProvider.eth.Contract(erc20abi, token)
    const takeCntr = new myProvider.eth.Contract(erc20abi, liquidity_token)
    const amount = toBN(money, token_decimals)


    if (type === 'save') {
      const allown = await saveCntr.methods.allowance(ethereum.selectedAddress, router_addr).call();
      const data = myContract.methods.deposit(token, amount).encodeABI()

      if (allown === '0') {
        return signCall(
          ethereum,
          token,
          saveCntr.methods.approve(router_addr,toBN()).encodeABI())
          .then(result => {
            ctrlMoney(ethereum, router_addr, data).then(result => {
              if (result) {
                message.success('交易成功')
                setVisible(false)
                setTimeout(() => {
                  setCtrlFlag(true)
                }, 5000);
              }
            })
          })
      }
      return ctrlMoney(ethereum, router_addr, data).then(result => {
        if (result) {
          message.success('交易成功')
          setTimeout(() => {
            setCtrlFlag(true)
          }, 5000);
          setVisible(false)
        }
      })
    }
    const allown = await takeCntr.methods.allowance(ethereum.selectedAddress, router_addr).call();
    const data = myContract.methods.withdraw(token, amount).encodeABI()
  
    if (allown === '0') {
      return signCall(
        ethereum,
        liquidity_token,
        takeCntr.methods.approve(router_addr,toBN()).encodeABI())
        .then(() => {
          ctrlMoney(ethereum, router_addr, data).then(result => {
            if (result) {
              message.success('交易成功')
              setTimeout(() => {
                setCtrlFlag(true)
              }, 5000);
              setVisible(false)
            }
          })
        })
    }
    return ctrlMoney(ethereum, router_addr, data).then(result => {
      if (result) {
        message.success('交易成功')
        setTimeout(() => {
          setCtrlFlag(true)
        }, 5000);
        setVisible(false)
      }
    })
  }

  const ctrlMoney = (eth, addr,data ) => {
    return signCall(eth,addr,data)
  }

  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.gunpool}`}>
        <Tabs
          size="large"
          activeKey={tabKey}
          onChange={key => {
            setTabKey(key)
            setCtrlFlag(true)
          }}
        >
          <TabPane tab="HECO" key="HECO" />
          <TabPane tab="OEC" key="OEC" />
          <TabPane tab="ETH" key="ETH" />
          <TabPane tab="BSC" key="BSC" />
        </Tabs>
        <Table 
          rowKey={() => uuid()}
          columns={columns}
          dataSource={tableData}
          pagination={false}
        />
      </div>
      <AmountModal
        visible={visible}
        type={type}
        item={item}
        provider={myProvider}
        addr={router_addr}
        handleOk={handleOk}
        handleCancel={() => {setVisible(false)}}
      />
    </>
  )
}

export default Gunpool
