import React, { useState } from 'react'
import uuid from 'uuid/dist/v4'
import { Select, Input, Button, Checkbox, Modal, Table, Divider } from 'antd'
import GlobalHeader from '@/components/GlobalHeader'
import globalStyles from '@/App.less'
import styles from './index.less'

const { Option } = Select;

const testData = [{
  block: 'xxx',
  hash: 'xxx',
  state: 'pending'
}, {
  block: 'xxx',
  hash: 'xxx',
  state: 'canceled'
}, {
  block: 'xxx',
  hash: 'xxx',
  state: 'finished'
}]

const HistoryModal = ({
  visible,
  onCancel
}) => {

  const columns = [{
    title: 'block',
    dataIndex: 'block'
  },{
    title: 'hash',
    dataIndex: 'hash'
  }, {
    title: 'state',
    dataIndex: 'state'
  }, {
    title: '操作',
    dataIndex: 'operation',
    render: (_, record, index) => {
      const { state } = record
      let btn = ''
      switch (state) {
        case 'pending':
          btn = (
            <>
              <div>取消</div>
              <Divider type="vertical" />
              <div>取出</div>
            </>
          )
          break;
        case 'canceled': 
          btn = <div>取回</div>
          break;
        case 'finished': 
          btn = <div>查看</div>
          break;
        default:
          btn = <div>查看</div>
          break;
      }
      return (<div className={styles.group}>{btn}</div>)
    }
  }]

  return (
    <Modal
      title="历史记录"
      visible={visible}
      onCancel={onCancel}
      footer={null}
      getContainer={false}
    >
      <Table 
        rowKey={() => uuid()}
        columns={columns}
        dataSource={testData}
        pagination={false}
      />
    </Modal>
  )
}


const Bridge = (props) => {
  const [visible, setVisible] = useState(false)
  return (
    <>
      <GlobalHeader {...props}/>
      <div className={`${globalStyles.mainContent} ${styles.bridge}`}>
          <div className={styles.info}>
            <div className={styles.infoLabel}>Token：</div>
            <div className={styles.infoValue}>
              <Select style={{ width: 150 }}>
                <Option value="USTD">USTD</Option>
                <Option value="BTC">BTC</Option>
                <Option value="ETH">ETH</Option>
              </Select>
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.infoLabel}>From Chain：</div>
            <div className={styles.infoValue}>
              <Select style={{ width: 150 }}>
                <Option value="ETH">ETH</Option>
                <Option value="BSC">BSC</Option>
                <Option value="HECO">HECO</Option>
              </Select>
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.infoLabel}>To Chain：</div>
            <div className={styles.infoValue}>
              <Select style={{ width: 150 }}>
                <Option value="ETH">ETH</Option>
                <Option value="BSC">BSC</Option>
                <Option value="HECO">HECO</Option>
              </Select>
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.infoLabel}>To Address：</div>
            <div className={styles.infoValue}>
              <Input />
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.infoLabel}>Amount：</div>
            <div className={styles.infoValue}>
              <Input />
            </div>
          </div>
          <div className={styles.info}>
            <div className={styles.infoLabel}>是否存款凭证：</div>
            <div className={styles.infoValue}>
              <Checkbox />
            </div>
          </div>
          <Button type="primary" size="large">提交</Button>
          <div className={styles.history} onClick={() => {setVisible(true)}}>查看历史记录</div>
          <HistoryModal visible={visible} onCancel={() => { setVisible(false)}}/>
        </div>
    </>
  )
}

export default Bridge
